.watch {
  border: 10px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: 50px auto;
  position: relative;
}

.watch__dot {
  z-index: 1;
  background-color: #f8c622;
  border-radius: 50%;
  width: 3vmin;
  height: 3vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.watch__min-arrow {
  transform-origin: bottom;
  background-color: #14a521;
  width: 8px;
  height: 40vmin;
  margin: 0 auto;
  animation-name: hand;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.watch__hour-arrow {
  transform-origin: top;
  background-color: #2076e7;
  width: 10px;
  height: 30vmin;
  margin: 0 auto;
  animation-name: hand;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes hand {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

